import Vue from "vue"
import mutations from "./mutations"
import actions from "./actions"
import Vuex from "vuex"

Vue.use(Vuex);
const store =new Vuex.Store({
    state:{
        topiclists:[
            {name:"all",desc:""},
            {name:"good",desc:""},
            {name:"share",desc:""},
            {name:"ask",desc:""},
            {name:"dev",desc:""},
            {name:"job",desc:""},
         ],
         isShow:false, //
         isLogined:false,//
         //
         LoginedMenu:{
           create:"",
           center:"",
           msgs:"",
           logout:""
    
         },

         //
         unLoginMenu:{
           login:""
         },
         loginname:"",
         accesstoken:"",
         author_id:"",
         count:""

    },
    //
    getters:{
        moreMenu(state){
            //,()
            //isLogined=true,
            if(state.isLogined){
              return state.LoginedMenu //
            }
           return state.unLoginMenu//
          },
        fileterTab(state){
          return function(tab){
            for (let i = 0; i <  state.topiclists.length; i++) {
              const element = state.topiclists[i]
              if(element.name == tab){
                return state.topiclists[i].desc
              }
            }
          }
        }
    },
    mutations,
    actions,
})

export default store;


